#startRoom body {
    background-color: black;
    height: 100%;
}

#startRoom h1 {
    font-size: xx-large;
    margin: 150px;
    margin-bottom: 10px;
    text-align: center;
    color: white;
}

#startRoom h2 {
    font-size: larger;
    margin: 80px;
    margin-bottom: 10px;
    text-align: center;
    color: white;
}

#startRoom p {
    color: white;
    text-align: center;
}
#startRoom a {
    color: rgb(137, 208, 255);
    text-align: center;
}

#startRoom {
    position: fixed; /* Sit on top of the page content */
    display: flex; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(0, 0, 0); /* Black background with opacity */
    z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
    flex-direction: column;
    align-items: center;
}

.startRoomButtonContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 159px;
}

#startRoom button {
    margin: 2px;
    display: flex;
    transition: cubic-bezier();
}
#startRoom button:hover {
    background-color: black;
    padding: 8px 16px;
    transition: cubic-bezier();
}